body {
  margin: 0;
  background-color: black;
}

* {
  /* -webkit-appearance: none; */
  -webkit-tap-highlight-color: transparent;
}


.webview-loading {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 50%;
  width: 100%;
  justify-content: center;
  transform: translateY(-50%);
  text-align: center;
}

.webview-loading > p {
  padding-top: 2rem;
  color: #d8d8d8;
}

.sandbox-text {
  color: white;
}